#canvas_wrapper{
    /*margin-left: 300px;*/
    // z-index: 10;
    position: relative;
    margin: 70px 30px 30px;
    background-color: #fff;
    -webkit-border-radius: 8px 8px 0 0;
    border-radius: 8px 8px 0 0;
    border-bottom: 30px solid #D4D4D4;
    // padding-top: 1px;
    &:before {
        content: '';
        display: block;
        height: 36px;
        background-color:#D4D4D4;
        -webkit-border-radius: 8px 8px 0 0;
        border-radius: 8px 8px 0 0;
    }
}
#canvas_wrapper_dots:before,
#canvas_wrapper_dots:after,
#canvas_wrapper_dots {
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    top: 14px;
    left: 35px;
    background-color:#f1c40f;
    -webkit-border-radius: 50%;
    border-radius: 50%;
}

#canvas_wrapper_dots:before {
    background-color: #e4344e;
    top: 0;
    left: -16px;
}

#canvas_wrapper_dots:after {
    background-color: #2ecc71;
    top: 0;
    left: 15px;
}

