@mixin switcherStyles {
  /*switcher styles*/
  #switcher {
      position: fixed;
      // padding: 0 10px 0 20px;
      top: 22%;
      right: -200px;
      width: 200px;
      // height: 466px;
      background-color: #404040;
      color: $lightColor;
      z-index: 9;
      font-size: 15px;
      padding-bottom: 20px;


      // -webkit-box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
      // box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);

      -webkit-transition: all 0.6s ease;
      -moz-transition: all 0.6s ease;
      -o-transition: all 0.6s ease;
      -ms-transition: all 0.6s ease;
      transition: all 0.6s ease;
      &.active{
          right: 0;
      }
      a {
          color: $mainColor;
      }
      .checkbox {
          margin: 4px 0;    
          line-height: 1.5;
      }
      label {
          // line-height: 1;
      }
      // input[type=checkbox] {top: 1px; position: relative;}
      h4 {
          line-height: 60px;
          font-size: 18px;
          text-align: center;
          color: #999999;
          background-color: #222;
          margin: 0;
          font-weight: 300;
          letter-spacing: 0;
          padding: 0 36px;
      }
      span[class^="rt-icon2-"]{
          // -webkit-box-shadow: 3px 0 5px 0 rgba(0,0,0,0.2);
          // box-shadow: 3px 0 5px 0 rgba(0,0,0,0.2);
          display: block;
          position: absolute;
          width: 60px;
          height: 60px;
          line-height: 60px;
          color: $lightColor;
          font-size: 24px;
          background-color: #222;
          text-align: center;
          left: -60px;
          top: 0;
          cursor: pointer;
          z-index: 2;
          &:hover {
            color: #999999;
          }
      }
      span[class="rt-icon2-delete-outline"] {
          background-color: transparent;
          // -webkit-box-shadow: none;
          // box-shadow: none;
          z-index: 1;
          left: -20px;
      }
      li {
          margin-bottom: 0px;
          padding: 0 4px;
      }
      p{
          font-weight: 300;
          font-size: 16px;
          line-height: 1;
          margin: 0 0 10px;
      }
      label {
        color: #999999;
      }
      ul {
        margin-bottom: 0;
      }
      .tooltip {
          min-width: 110px;
      }
      .tooltip-arrow {
        display: none;
      }
  }
  #switcher-version a {
    padding: 13px 14px;
    display: inline-block;
    line-height: 1;
    font-size: 13px;
    font-weight: 400;

    text-transform: uppercase;
    &:hover {color: #e4344e !important;}
  }
  #switcher-version a.light {
  background-color: $lightColor;
  color: $darkgreyColor;
  // border: 1px solid $lightColor;
  }
  #switcher-version a.light:hover {
      // background-color: transparent;
      // color: $lightColor;
  }
  #switcher-version a.dark {
      color: $lightColor;
      background-color: #262626;
      // border: 1px solid $lightColor;
  }
  #switcher-version a.dark:hover {
      // color: $darkgreyColor;
      // background-color: $lightColor;
  } 

  // #f5bb2c
  // #e23751
  // #16a4d6

  #switcher-colors a {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  // border-radius: 50%;
  background-color: #f5bb2c;
   overflow:hidden;
   &:hover {
    opacity: 0.7;
   }
      &:after {
          content: '';
          position: absolute;
          width: 30px;
          height: 74px;
          right: 0;
          // top: 0;
          background-color: #e23751;
          -moz-transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
          -o-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          transform: rotate(45deg);
      }
  }
  #switcher-colors a.color2 {
  background-color: #e23751;
      &:after {
          background-color: #16a4d6;
      }
  }
  #switcher-colors a.color3 {
  background-color: #16a4d6;
      &:after {
          background-color: #f5bb2c;
      }
  }
  #switcher-colors  {
  line-height: 0;
   // height: 40px;
   // width: 48px;
   // padding: 0 4px;
   // margin-right: 8px;

  }
  #switcher-patterns a {
      display: block;
      line-height: 1;
      height: 40px;
      width: 40px;
      overflow: hidden;
      // border-radius: 50%;
      background-color: #ffffff;
      img {
          opacity: 0;
      }
      &:hover {
        opacity: 0.7;
      }
  }
  #switcher-patterns li:first-child a img{
      opacity: 1;
  }

  #switcher_accent_color,
  #switcher_color_scheme,
  #switcher_layout,
  #switcher_patterns {
      padding: 26px 30px 0px;
  }

  #switcher_color_scheme,
  #switcher_patterns {
      // background-color: #333333;
  }

  ///////////////////////
  // delete on production

  .checkbox-slider--b-flat {
    position: relative;
  }
  .checkbox-slider--b-flat input {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 0%;
    margin: 0 0;
    cursor: pointer;
    zoom: 1;
    -webkit-opacity: 0;
    -moz-opacity: 0;
    opacity: 0;
    filter: alpha(opacity=0);
  }
  .checkbox-slider--b-flat input + span {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .checkbox-slider--b-flat input + span:before {
    position: absolute;
    left: 0px;
    display: inline-block;
  }
  .checkbox-slider--b-flat input + span > h4 {
    display: inline;
  }
  .checkbox-slider--b-flat input + span {
    padding-left: 40px;
  }
  .checkbox-slider--b-flat input + span:before {
    content: "";
    height: 20px;
    width: 40px;
    background: $lightColor;
    // background: rgba(100, 100, 100, 0.2);
    // box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.8);
    // transition: background 0.2s ease-out;
  }
  .checkbox-slider--b-flat input + span:after {
    width: 20px;
    height: 20px;
    position: absolute;
    left: 0px;
    top: 0;
    display: block;
    background: #ffffff;
    transition: margin-left 0.4s ease-in-out;
    text-align: center;
    font-weight: bold;
    content: "";
  }
  .checkbox-slider--b-flat input:checked + span:after {
    margin-left: 20px;
    content: "";
  }
  .checkbox-slider--b-flat input:checked + span:before {
    transition: background 0.2s ease-in;
  }
  .checkbox-slider--b-flat input + span {
    padding-left: 30px;
  }
  .checkbox-slider--b-flat input + span:before {
    border-radius: 20px;
    width: 40px;
  }
  .checkbox-slider--b-flat input + span:after {
    background: $mainColor;
    content: "";
    width: 20px;
    border: solid transparent 2px;
    background-clip: padding-box;
    border-radius: 20px;
  }
  .checkbox-slider--b-flat input:not(:checked) + span:after {
    // -webkit-animation: popOut ease-in 0.3s normal;
    //         animation: popOut ease-in 0.3s normal;
  }
  .checkbox-slider--b-flat input:checked + span:after {
    content: "";
    margin-left: 20px;
    border: solid transparent 2px;
    background-clip: padding-box;
    // -webkit-animation: popIn ease-in 0.3s normal;
    //         animation: popIn ease-in 0.3s normal;
  }
  .checkbox-slider--b-flat input:checked + span:before {
    background: $lightColor;
  }
  .checkbox-slider--b-flat.checkbox-slider-md input + span:before {
    border-radius: 30px;
  }
  .checkbox-slider--b-flat.checkbox-slider-md input + span:after {
    border-radius: 30px;
  }
  .checkbox-slider--b-flat.checkbox-slider-lg input + span:before {
    border-radius: 40px;
  }
  .checkbox-slider--b-flat.checkbox-slider-lg input + span:after {
    border-radius: 40px;
  }
  .checkbox-slider--b-flat input + span:before {
    box-shadow: none;
  }
}