// variables
//main accent colors moved to _accent_colorsX.scss

//Colors
//text in light section
$fontColor: #9a9a9a;
//background color for light section and font color in dark section
$lightColor: #ffffff;
//color for headings in light section and for background in dark section (.ds)
$darkgreyColor: #323232;
//background color for light muted section (grey - .ls.ms)
$greyColor: #f2f2f2;
//background color for dark muted section (.ds.ms)
$darkColor: #1a1a1a;
//border colors for elements in light section (and .with_border elements)
$borderColor: #e1e1e1;
$borderColorRGBA: rgba(#000, 0.05);
//background color for elements in light section (.with_background)
$backgroundColor: #f5f5f5;

//section variables
///////// dark
$darkFontColor: #9a9a9a;
    //for .with_border
$darkBorderColor: rgba(255, 255, 255, 0.1);
	// for .with_background, .muted_section
$darkBackgroundColor: rgba(#fff, 0.06);

///////// color
// $colorFontColor: lighten($mainColor, 30%);
$colorBorderColor: rgba(255, 255, 255, 0.1);

@mixin elementTransition($proprety: all, $duration: 0.2s, $time_function: ease-in-out, $delay: 0s) {
    -webkit-transition: $proprety $duration $time_function $delay;
    -moz-transition: $proprety $duration $time_function $delay;
    -o-transition: $proprety $duration $time_function $delay;
    -ms-transition: $proprety $duration $time_function $delay;
    transition: $proprety $duration $time_function $delay;
}